.home {
    display: flex;
    flex-direction: column;
    // justify-content: space-between;
    height: 100vh;
    gap: 15px;

    @include rmin($phone-s) {
        gap: 25px;
    }

    @include rmin($phone-m) {
        gap: 35px;
    }

    &__logo {
        width: 85%;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__top {
    }
    &__center {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;

        strong {
            margin-top: -10px;
            font-size: 20px;
            font-weight: 300;
        }
    }
    &__bottom {
        padding-bottom: 20px;

        a {
            &:not(:first-child) {
                margin-top: 10px;
            }
        }
    }
}
