.feedback {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;

    padding-top: 50px;
    padding-bottom: 125px;

    @include rmin($phone-m) {
        gap: 40px;

        padding-top: 75px;
    }

    &__bottom {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;

        strong,
        p {
            display: flex;
            padding-left: 15px;
            padding-right: 15px;
            text-align: center;
        }

        & > strong {
            font-family: $font-secondary;
            font-size: 28px;
            line-height: 28px;
        }
        p {
            font-size: 18px;
            line-height: 22px;
        }

        a {
            margin-top: 5px;
            width: 100%;
        }

        @include rmin($phone-m) {
            & > strong {
                font-size: 38px;
                line-height: 38px;
            }

            a {
                margin-top: 15px;
            }
        }
    }

    &__slider {
        width: calc(100% + 20px);
        margin-left: -10px;
        margin-right: -10px;
    }

    .glide {
        &__slides {
            height: 175px;

            @include rmin($phone-s) {
                height: 195px;
            }

            @include rmin($phone-m) {
                height: 195px;
            }
        }

        &__slide {
            height: 100%;
            border-radius: 15px;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
