.home .glide {
    margin-left: -20px;
    margin-right: -20px;

    width: calc(100% + 40px);
    height: 375px;

    @include rmin($phone-s) {
        height: 425px;
    }

    @include rmin($phone-m) {
        height: 452px;
    }

    border-bottom-left-radius: 250px;
    border-bottom-right-radius: 250px;
    overflow: hidden;

    &__slide {
        img {
            display: flex;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
