.nav {
    z-index: 1;
    position: fixed;
    bottom: 10px;
    left: 5px;
    right: 5px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: calc(100% - 10px);
    height: 60px;
    padding-left: 15px;
    padding-right: 15px;

    border: 0.5px solid var(--gray-100);
    background-color: hsla(0, 0%, 98%, 0.97);
    box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.15);
    border-radius: 50px;

    @include rmin($tablet) {
        margin-left: auto;
        margin-right: auto;
        max-width: 414px;
    }

    &__item {
        display: flex;
        justify-content: center;
        flex: 1;

        a {
            display: flex;
            flex-direction: column;
            align-items: center;

            text-decoration: none;
            color: var(--gray-400);

            svg {
                width: 25px;
                height: 25px;
                fill: currentColor;
            }

            span {
                font-size: 13px;
                font-weight: 700;
            }

            &:hover {
                color: var(--red);
            }
        }

        &--active {
            a {
                color: var(--red);

                svg {
                    fill: currentColor;
                }
            }
        }
    }

    &__item--special-ice {
        position: relative;

        span {
            display: flex;
            align-items: center;
            height: 14px;
            padding: 2px 10px 0px 10px;
            margin-top: -12px;

            border-radius: 100px;
            color: var(--white);
            background: var(--gradient);
        }

        .blur {
            z-index: -1;
            position: absolute;
            width: 100%;
            height: 100%;
            filter: blur(15px);
        }
    }
}
