.item {
    &__simple-list-wrap {
        display: flex;
        flex-direction: column;
        gap: 35px;
    }
    &__simple-list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__simple {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        gap: 2px;

        strong {
            text-align: center;
            font-size: 26px;
            font-family: $font-secondary;
        }

        &-info {
            display: flex;
            align-items: center;
            gap: 15px;

            span {
                font-size: 18px;
                color: var(--gray-500);
            }
        }
    }
}
