.home__top {
  animation: slide-in-top 1.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation-delay: 0.5s;
}

.home__center {
  animation: slide-in-bottom 1.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation-delay: 1s;
}

.home__bottom a {
  animation-delay: 0.35s;
  animation-duration: 2s;
}

.home__bottom a:nth-child(1), .home__bottom a:nth-child(2), .home__bottom a:nth-child(3), .home__bottom a:nth-child(4), .home__bottom a:nth-child(5) {
  animation: slide-in-bottom 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.home__bottom a:nth-child(1) {
  animation-delay: 1.25s;
}

.home__bottom a:nth-child(2) {
  animation-delay: 1.35s;
}

.home__bottom a:nth-child(3) {
  animation-delay: 1.45s;
}

.home__bottom a:nth-child(4) {
  animation-delay: 1.55s;
}

.slide-in-bottom {
  animation: slide-in-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.slide-in-top {
  animation: slide-in-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes slide-in-bottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slide-in-top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

