.item {
    position: relative;
    height: 550px;
    max-height: 570px;
    margin-top: 15px;

    @include rmin($phone-m) {
        height: 600px;
    }

    &__img-wrap {
        position: relative;
        border-radius: 250px;
        overflow: hidden;
        height: 100%;

        img {
            display: flex;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &::before {
            content: "";
            position: absolute;
            bottom: 0;
            height: 320px;
            width: 100%;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000 100%);
        }
    }
    &__info {
        position: absolute;
        bottom: 0;

        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding-left: 25px;
        padding-right: 25px;
        padding-bottom: 45px;

        color: var(--white);

        strong {
            padding-left: 10px;
            padding-right: 10px;
            font-family: $font-secondary;
            font-size: 34px;
            line-height: 34px;
            text-align: center;

            @include rmin($phone-s) {
                font-size: 36px;
                line-height: 36px;
            }
            @include rmin($phone-m) {
                font-size: 42px;
                line-height: 40px;
            }
        }

        &-weight,
        &-price {
            font-weight: 300;
            opacity: 0.65;
        }

        &-weight {
            font-size: 20px;
        }
        &-price {
            display: flex;
            align-items: center;
            height: 35px;
            padding: 2px 15px 0;
            margin-top: 5px;

            font-size: 22px;
            border: 1px solid currentColor;
            border-radius: 25px;
        }
    }
}
